import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import TextFieldGroup from '../../common/TextFieldGroup';
import TextAreaFieldGroup from '../../common/TextAreaFieldGroup';
import InputGroup from '../../common/InputGroup';
import SelectListGroup from '../../common/SelectListGroup';
import { createProfile, resetErrors } from '../../../actions/profileActions';
class CreateProfile extends Component {
  constructor(props) {
    super(props);
    this.state = {
      displaySocialInputs: false,
      name: '',
      location: '',
      age: '',
      bio: '',
      status: '',
      website: '',
      twitter: '',
      facebook: '',
      youtube: '',
      instagram: '',
      errors: {}
    };
    // this.onChange = this.onChange.bind(this);
    // this.onSubmit = this.onSubmit.bind(this);
  }

  static getDerivedStateFromProps = (nextProps, prevState) =>
    nextProps.errors !== prevState.errors
      ? {
          errors: nextProps.errors
        }
      : null;
  componentWillUnmount = () => this.props.resetErrors();

  onChange = e => this.setState({ [e.target.name]: e.target.value });

  onSubmit = e => {
    e.preventDefault();
    const userData = {
      name: this.state.name,
      location: this.state.location,
      age: this.state.age,
      bio: this.state.bio,
      status: this.state.status,
      website: this.state.website,
      twitter: this.state.twitter,
      instagram: this.state.instagram,
      facebook: this.state.facebook,
      youtube: this.state.youtube
    };
    this.props.createProfile(userData, this.props.history);
  };
  render() {
    const { errors, displaySocialInputs } = this.state;
    const options = [
      { label: '    ', value: 0 },
      { label: '  ', value: '  ' },
      { label: '1-2', value: '1-2' },
      { label: '3-5', value: '3-5' },
      { label: '6-10', value: '6-10' },
      { label: ' 10', value: ' 10' }
    ];
    let SocialInputs = displaySocialInputs ? (
      <div>
        <TextFieldGroup
          placeholder=""
          name="website"
          value={this.state.website}
          onChange={this.onChange}
          error={errors.website}
          info="   ,   "
        />
        <InputGroup
          placeholder="Twitter Profile URL"
          name="twitter"
          icon="fab fa-twitter"
          value={this.state.twitter}
          onChange={this.onChange}
          error={errors.twitter}
        />
        <InputGroup
          placeholder="Facebook Profile URL"
          name="facebook"
          icon="fab fa-facebook"
          value={this.state.facebook}
          onChange={this.onChange}
          error={errors.facebook}
        />
        <InputGroup
          placeholder="Instagram Profile URL"
          name="instagram"
          icon="fab fa-instagram"
          value={this.state.instagram}
          onChange={this.onChange}
          error={errors.instagram}
        />
        <InputGroup
          placeholder="YouTube Profile URL"
          name="youtube"
          icon="fab fa-youtube"
          value={this.state.youtube}
          onChange={this.onChange}
          error={errors.youtube}
        />
      </div>
    ) : null;

    return (
      <div className="fill-form">
        <div className="container mt-150">
          <div className="row">
            <div className="col-md-8 m-auto">
              <a href="/" className="btn btn-light">
                
              </a>
              <h1 className="display-4 text-center"> </h1>
              <p className="lead text-center">
                   ,   
              </p>
              <form noValidate onSubmit={this.onSubmit}>
                <TextFieldGroup
                  name="name"
                  type="text"
                  placeholder=""
                  error={errors.name}
                  value={this.state.name}
                  onChange={this.onChange}
                />
                <TextFieldGroup
                  placeholder=""
                  name="location"
                  value={this.state.location}
                  onChange={this.onChange}
                  error={errors.location}
                  info="  "
                />
                <TextFieldGroup
                  placeholder=" "
                  name="age"
                  value={this.state.age}
                  onChange={this.onChange}
                  error={errors.age}
                  info="  "
                />
                <SelectListGroup
                  placeholder="Status"
                  name="status"
                  value={this.state.status}
                  onChange={this.onChange}
                  options={options}
                  error={errors.status}
                  info="     "
                />
                <TextAreaFieldGroup
                  placeholder=" "
                  name="bio"
                  value={this.state.bio}
                  onChange={this.onChange}
                  error={errors.bio}
                  info="    "
                />
                <div
                  onClick={() => {
                    this.setState(prevState => ({
                      displaySocialInputs: !prevState.displaySocialInputs
                    }));
                  }}
                  className="mb-3"
                >
                  <button type="button" className="btn btn-light">
                     
                  </button>
                </div>
                {SocialInputs}
                <input
                  type="submit"
                  value=" "
                  className="btn btn-info btn-block mt-4 mb-50"
                />
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
CreateProfile.propTypes = {
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired,
  createProfile: PropTypes.func.isRequired,
  resetErrors: PropTypes.func.isRequired
};
const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});
export default connect(
  mapStateToProps,
  { createProfile, resetErrors }
)(CreateProfile);
